.register {
    width: 500px;
    margin: auto;
    padding-top: 50px;
}
.register input {
    display: block;
    width: 100%;
    height: 37px;
    font-size: 24px;
    margin-bottom: 20px;
}
.register .err {
    margin-left: 15px;
    color: red;
    float: right;
}
.register .btn {
    padding-left: 15px;
    padding-right: 15px;
}
.register .top-err {
    color: red;
    width: fit-content;
    margin: auto;
}
.register .top-err .li {
    width: fit-content;
}