.notes .menu .menu-editor:after {
    content: "";
    display: block;
    clear: both;
}
.notes .main {
    width: 80%;
    max-width: calc(100% - 240px);
    float: right;
    position: relative;
    /*margin-top: 50px;*/
}
.notes .md {
    padding:20px 40px 40px;
}
.header {
    width: 100%;
    height: 50px;
    position: fixed;

    background: #fff;
    border-bottom: 1px solid #d4dadf;
    box-shadow: 0 1px 1px 0 rgba(116, 129, 141, 0.1);
    z-index: 2;
}
.header .title {
    float: left;
    line-height: 50px;
    margin-left: 20px;
    font-size: 20px;
}
.header .title i {
    margin-right: 10px;
}
.header ul.nav {
    margin: 0 40px;
    padding: 0;
    float: left;
}
.header ul.nav li{
    float: left;
    margin-right: 20px;
    list-style: none;
    line-height: 50px;
}
.header .topmenu {
    float: right;
    margin-left: auto;
    margin-top: auto;
    margin-bottom: auto;
    margin-right: 50px;
}
.header .topmenu li {
    float: left;
    list-style: none;
    margin-right: 15px;
    line-height: 50px;
}
.register {
    width: 500px;
    margin: auto;
    padding-top: 50px;
}
.register input {
    display: block;
    width: 100%;
    height: 37px;
    font-size: 24px;
    margin-bottom: 20px;
}
.register .err {
    margin-left: 15px;
    color: red;
    float: right;
}
.register .btn {
    padding-left: 15px;
    padding-right: 15px;
}
.register .top-err {
    color: red;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    margin: auto;
}
.register .top-err .li {
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
}
.login {
    width: 500px;
    margin: auto;
    padding-top: 50px;
}
.login input {
    display: block;
    width: 100%;
    height: 37px;
    font-size: 24px;
    margin-bottom: 20px;
}
.login .err {
    margin-left: 15px;
    color: red;
    float: right;
}
.login .btn {
    padding-left: 15px;
    padding-right: 15px;
}
.login .top-err {
    color: red;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    margin: auto;
}
.login .top-err .li {
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
}
.syndata {
    width: 100%;
}
.syndata ul {
    width: 200px;
    border: 1px solid #dfe2e5;
    margin: auto;
    margin-top: 20px;
}
.syndata button{
    display: block;
    margin: auto;
    margin-top: 20px;
}
