.notes .menu {
    background-color: #F5F7F9;
    height: calc(100% - 50px);
    width: 20%;
    top: 50px;
    min-width: 240px;
    padding: 10px;
    position: fixed;
    overflow-y: scroll;
}
.notes .menu dl {
    margin: 0;
    color: rgb(157, 170, 182);
    font-size: 15px;
    position: relative;
    margin-left: 15px;
}
.notes .menu>dl>dt {
    color: #5c6975;
}
.notes .menu dt,
.notes .menu dd {
    line-height: 37px;
    /*padding: 0px 15px;*/
    cursor: pointer;
    font-weight: bold;
}
.notes .menu dd {
    border: 1px solid transparent;
    border-left: 1px solid #E6ECF1;
    margin-left: 0px;
    overflow-wrap: break-word;
}

.notes .menu dd:not(.has-child) {
    padding-left: 15px;
    padding-right:15px;
}
.notes .menu dt:not(.has-child):hover,
.notes .menu dd:not(.has-child):hover {
    background-color: rgb(230, 236, 241);
}
.notes .menu dt:not(.has-child).active,
.notes .menu dd:not(.has-child).active {
    background-color: #ffffff;
    border: 1px solid #E6ECF1;
    color: rgb(116, 76, 188);
}
.notes .menu dt {
    padding-right:15px;
    border: 1px solid transparent;
}
.notes .menu .fa {
    float: right;
    line-height: 37px;
    position: relative;
}
.notes .menu .fa ul {
    position: absolute;
    background: #ffffff;
    right: 0;
    top: 10px;
    width: 130px;
    padding: 10px;
    z-index: 1;
    cursor: auto;
    box-shadow: 2px 2px 10px #888888;
}
.notes .menu .fa ul li {
    list-style: none;
    line-height: 1.5;
    cursor: pointer;
    color: #5c6975;
}
.notes .menu .fa ul li:hover {
    color: rgb(116, 76, 188);
    background-color: #dfe2e5;
}
.notes .menu .btn {
    box-shadow: inset 0 1px 0 rgba(255,255,255,0.2);
    background-color: #ffffff;
    border-color: #198764;
    text-align: center;
    height: 37px;
    line-height: 37px;
    border-radius: 5px;
    cursor: pointer;
    border: 1px solid #1d9d74;
}
.notes .menu .btn:hover {
    color: #fff;
    background-color: #1d9d74;
}
.notes .menu .menu-editor {
    position: relative;
    padding: 0;
}
.notes .menu .menu-editor input {
    line-height: 37px;
    height: 37px;
    width: calc(100% - 15px);
    float: left;
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}
