.header {
    width: 100%;
    height: 50px;
    position: fixed;

    background: #fff;
    border-bottom: 1px solid #d4dadf;
    box-shadow: 0 1px 1px 0 rgba(116, 129, 141, 0.1);
    z-index: 2;
}
.header .title {
    float: left;
    line-height: 50px;
    margin-left: 20px;
    font-size: 20px;
}
.header .title i {
    margin-right: 10px;
}
.header ul.nav {
    margin: 0 40px;
    padding: 0;
    float: left;
}
.header ul.nav li{
    float: left;
    margin-right: 20px;
    list-style: none;
    line-height: 50px;
}
.header .topmenu {
    float: right;
    margin-left: auto;
    margin-top: auto;
    margin-bottom: auto;
    margin-right: 50px;
}
.header .topmenu li {
    float: left;
    list-style: none;
    margin-right: 15px;
    line-height: 50px;
}